import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { DataService } from '../../shared/data.service'
import { SocketService } from '../../shared/socketio/socket.service'
import { Subscription } from 'rxjs/Subscription'

declare var $: any;
@Component({
  selector: 'app-adv-setting',
  templateUrl: './adv-setting.component.html',
  styleUrls: ['./adv-setting.component.css']
})

export class AdvSettingComponent implements OnInit {
  @ViewChild('dateinput') dateInput: ElementRef;
  @ViewChild('timeinput') timeInput: ElementRef;
  @ViewChild('cirtime') cirTimeInput: ElementRef;
  @ViewChild('cooktime') cookTimeInput: ElementRef;

  public eccontrol: boolean = false;
  public phcontrol: boolean = false;
  public co2control: boolean = false;
  public fillwater: boolean = false;
  public circulating: boolean = false;
  public timercontrol: boolean = false;
  public circulatingTime: number = 300;
  public cookingTime: number = 300;
  public watersystem:boolean = false;

  public ecpump: number = 85;
  public phpump: number = 85;
  public circulatingpump: number = 85;
  public brightness: number = 100;


  public date: string;
  public time: string;
  public subscription: Subscription;
  public dtsub: Subscription;
  constructor(private dataService: DataService, private io: SocketService) {
    this.io.socket.emit('REQ_SETTING');
  }

  ngOnInit() {
    $.fn.numpad.defaults.gridTpl = '<table class="table modal-content" style="width: 50%; height: 50%;"></table>';
    $.fn.numpad.defaults.backgroundTpl = '<div class="modal-backdrop in"></div>';
    $.fn.numpad.defaults.displayTpl = '<input type="text" class="form-control" style="width: 100%; height: 100%; font-size: 250%;" />';
    $.fn.numpad.defaults.buttonNumberTpl = '<button type="button" class="btn btn-default" style="width: 100%; height: 100%;"></button>';
    $.fn.numpad.defaults.buttonFunctionTpl = '<button type="button" class="btn" style="width: 100%; height: 100%;"></button>';
    $.fn.numpad.defaults.onKeypadCreate = function () { $(this).find('.done').addClass('btn-primary'); };

    $('#circulating-btn').numpad({
      target: $('#circulating')
    });
    $('#cooking-btn').numpad({
      target: $('#cooking')
    });

    $('.clockpicker').clockpicker();
    $('[data-toggle="datepicker"]').datepicker(
      {
        format: 'yyyy-mm-dd'
      }
    );
    this.subscription = this.dataService.settingSubject.asObservable().subscribe(data => {
      console.log(data);
      this.eccontrol = (data['ec-control'] == 1) ? true : false;
      this.phcontrol = (data['ph-control'] == 1) ? true : false;
      this.co2control = (data['co2-control'] == 1) ? true : false;
      this.circulating = (data['circulating'] == 1) ? true : false;
      this.fillwater = (data['fill-water'] == 1) ? true : false;
      this.timercontrol = (data['timer-mode'] == 1) ? true : false;
      this.circulatingTime = data['circulating-time'];
      this.cookingTime = data['cooking-time'];
      this.watersystem = (data['water-system'] == 1) ? true : false;
      console.log(this.cookingTime, this.circulatingTime)
    })


    this.dtsub = this.dataService.datetimeSubject.asObservable().subscribe(data => {

      this.date = data.date;
      this.time = data.time.substring(0, data.time.length - 3);

      this.dtsub.unsubscribe();
    });
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.subscription.unsubscribe();
  }


  updateTime(date, time): void{

    var data = {
      'date': date,
      'time': time
    }
    console.log(data);
    this.io.socket.emit('DATETIME_UPDATE', data);
  }

  updateSysParam(): void {
    var data = {
      circulatingTime: this.cirTimeInput.nativeElement.value,
      cookingTime: this.cookTimeInput.nativeElement.value
    }
    this.io.socket.emit('UPDATE_SYS_PARAM', data);
  }

  Co2Control(val): void {
    this.io.socket.emit('CHANGE_CO2_CONTROL', { mode: (val) ? 1 : 0 })
  }

  ECControl(val): void {
    this.io.socket.emit('CHANGE_EC_CONTROL', { mode: (val) ? 1 : 0 })
  }

  PHControl(val): void {
    this.io.socket.emit('CHANGE_PH_CONTROL', { mode: (val) ? 1 : 0 })
  }

  Circulating(val): void {
    this.io.socket.emit('CHANGE_CIRCULATING', { mode: (val) ? 1 : 0 })
  }

  FillWater(val): void {
    this.io.socket.emit('CHANGE_FILLWATER', { mode: (val) ? 1 : 0 })
  }

  TimerControl(val): void {
    this.io.socket.emit('CHANGE_TIMER_MODE', { mode: (val) ? 1 : 0 })
  }

  WaterSystem(val): void {
    this.io.socket.emit('CHANGE_WATER_SYSTEM', { mode: (val) ? 1 : 0 })
  }

}



