import { Component, OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { DataService } from '../../shared/data.service';
import { SocketService } from '../../shared/socketio/socket.service';
declare var $:any;
@Component({
  selector: 'app-basic-configure',
  templateUrl: './basic-configure.component.html',
  styleUrls: ['./basic-configure.component.css']
})
export class BasicConfigureComponent implements OnInit {
  subscription: Subscription;
  @ViewChild('ecsetpoint') ecsetpoint: ElementRef;
  @ViewChild('ecworking') ecworking: ElementRef;
  @ViewChild('phsetpoint') phsetpoint: ElementRef;
  @ViewChild('phworking') phworking: ElementRef;
  @ViewChild('co2setpoint') co2setpoint: ElementRef;
  settingData: any = {
    'co2-setpoint': 950,
    'ec-setpoint': 1.5,
    'ec-working': 10,
    'ph-setpoint': 6.5,
    'ph-working': 6.5
  }

  constructor(private dataService: DataService, private io: SocketService) {
    this.io.socket.emit('REQ_SETTING');
  }
ngOnInit() {

   $.fn.numpad.defaults.gridTpl = '<table class="table modal-content" style="width: 50%; height: 50%;"></table>';
    $.fn.numpad.defaults.backgroundTpl = '<div class="modal-backdrop in"></div>';
    $.fn.numpad.defaults.displayTpl = '<input type="text" class="form-control" style="width: 100%; height: 100%; font-size: 250%;" />';
    $.fn.numpad.defaults.buttonNumberTpl = '<button type="button" class="btn btn-default" style="width: 100%; height: 100%;"></button>';
    $.fn.numpad.defaults.buttonFunctionTpl = '<button type="button" class="btn" style="width: 100%; height: 100%;"></button>';
    $.fn.numpad.defaults.onKeypadCreate = function () { $(this).find('.done').addClass('btn-primary'); };

    $('#ecsettpoint-btn').numpad({
      target: $('#ecsettpoint')
    });
    $('#phsettpoint-btn').numpad({
      target: $('#phsettpoint')
    });

    $('#co2setpoint-btn').numpad({
      target: $('#co2setpoint')
    });
    $('#ecworking-btn').numpad({
      target: $('#ecworking')
    });
    $('#phworking-btn').numpad({
      target: $('#phworking')
    });

   
    this.subscription = this.dataService.settingSubject.asObservable().subscribe( (data)=>{
      console.log(data);
      this.settingData['co2-setpoint'] = data['co2-setpoint'];
      this.settingData['ec-setpoint'] = data['ec-setpoint'];
      this.settingData['ph-setpoint'] = data['ph-setpoint'];
      this.settingData['ec-working'] = data['ec-working'];
      this.settingData['ph-working'] = data['ph-working'];


      this.ecsetpoint.nativeElement.value = data['ec-setpoint'];
      this.ecworking.nativeElement.value = data['ec-working'];
      this.phsetpoint.nativeElement.value = data['ph-setpoint'];
      this.phworking.nativeElement.value = data['ph-working'];
      this.co2setpoint.nativeElement.value = data['co2-setpoint'];
    })
  }

  update():void {
    var d = {
      'ec-setpoint': parseFloat(this.ecsetpoint.nativeElement.value),
      'ec-working':parseInt(this.ecworking.nativeElement.value),
      'ph-setpoint':parseInt(this.phsetpoint.nativeElement.value),
      'ph-working':parseInt(this.phworking.nativeElement.value),
      'co2-setpoint':parseInt(this.co2setpoint.nativeElement.value)
    }
     console.log(d);
     this.io.socket.emit('UPDATE_BASIC_CONFIG', d);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}



