import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {settingRouting} from './setting.routing'
import { SettingComponent } from './setting.component';
import { BasicConfigureComponent } from './basic-configure/basic-configure.component';
import { MenuListComponent } from './menu-list/menu-list.component';
import { LedTimerComponent } from './led-timer/led-timer.component';
import { AdvSettingComponent } from './adv-setting/adv-setting.component';
import { DataLoggerComponent } from './data-logger/data-logger.component'
import { UiSwitchModule } from '../../../node_modules/angular2-ui-switch/src'
import { FormsModule } from '@angular/forms';

@NgModule({
  imports: [
    CommonModule,
    settingRouting,
    UiSwitchModule,
    FormsModule
  ],
  declarations: [
    SettingComponent,
    BasicConfigureComponent,
    MenuListComponent,
    LedTimerComponent,
    AdvSettingComponent,
    DataLoggerComponent
  ]
})
export class SettingModule { }



