import { Component, OnInit, OnDestroy, trigger,transition,style,animate,sequence } from '@angular/core';
import { DataService } from '../../shared/data.service'
import {SocketService} from '../../shared/socketio/socket.service'

import { Subscription } from 'rxjs/Subscription'
declare var $:any;
@Component({
  selector: 'app-led-timer',
  templateUrl: './led-timer.component.html',
  styleUrls: ['./led-timer.component.css'],
  animations: [
     trigger('anim', [
        transition('* => void', [
          style({ height: '*', opacity: '1', transform: 'translateX(0)', 'box-shadow': '0 1px 4px 0 rgba(0, 0, 0, 0.3)'}),
          sequence([
            animate(".25s ease", style({ height: '*', opacity: '.2', transform: 'translateX(20px)', 'box-shadow': 'none'  })),
            animate(".1s ease", style({ height: '0', opacity: 0, transform: 'translateX(20px)', 'box-shadow': 'none'  }))
          ])
        ]),
        transition('void => active', [
          style({ height: '0', opacity: '0', transform: 'translateX(20px)', 'box-shadow': 'none' }),
          sequence([
            animate(".1s ease", style({ height: '*', opacity: '.2', transform: 'translateX(20px)', 'box-shadow': 'none'  })),
            animate(".35s ease", style({ height: '*', opacity: 1, transform: 'translateX(0)', 'box-shadow': '0 1px 4px 0 rgba(0, 0, 0, 0.3)'  }))
          ])
        ])
    ])
  ]
})
export class LedTimerComponent implements OnInit, OnDestroy {

  public subscription:Subscription;
  public timerList:any = [];
  public timerMode:boolean = false;
  public timerSwitch:boolean = false;
   private animationInitialized: boolean = false;
  constructor(private dataService: DataService, private io:SocketService) {
    this.io.socket.emit('REQ_SETTING');
  }

  ngOnInit() {
    this.subscription = this.dataService.settingSubject.asObservable().subscribe(data=>{
      this.timerList = data['timer-list'];
      this.timerMode = (data['timer-mode'] == 1)?true:false;
      this.timerSwitch = (data['timer-switch'] == 1)?true:false;
    })
    $('.clockpicker').clockpicker();
  }

  ngOnDestroy() {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    this.subscription.unsubscribe();
  }

  updateTimer():void{
    console.log(this.timerList);
    if(this.timerList.length <= 0){
      alert('Fail: Timer list number should not be zero.')
      this.io.socket.emit('REQ_SETTING');
      return;
    }
    this.io.socket.emit('UPDATE_LED_TIMER', this.timerList);
  }

  deleteTimer(index):void{
    if(index >= 0 ){
      this.timerList.splice(index,1);
    }
  }

  addTimer(start, end):void {
    console.log(start,end);
    var startMin = this.timerStringToMin(start);
    var stopMin = this.timerStringToMin(end);

    if(startMin >= stopMin){
      alert('Fail: Start time should more than stop time.')
      return;
    }
    var flag = false;
    this.timerList.forEach(timer=>{
      var endTime = timer.end;
      if(endTime >= startMin){
        flag = true;
      }
    })
    if(flag){
       alert('The new timer should more than the last of timer list.')
       return;
    }
    
    var timer = {
      'start': startMin,
      'end': stopMin
    }
    this.timerList.push(timer);
  }

  changeTimerMode(val):void {
    var data = {
      mode: (val)?1:0
    }
    console.log(data);
    this.io.socket.emit('CHANGE_TIMER_MODE', data);
  }
  changeTimerSwitch(val):void {
     var data = {
      mode: (val)?1:0
    }
    console.log(data);
    this.io.socket.emit('CHANGE_TIMER_SWITCH', data);
  }

  MinToTimeFormat(value):string {
    var val = parseInt(value);
    var hour = Math.floor(val/60);
    var min  = val % 60;
    var hstr,mstr;
    if(hour < 10 ){
      hstr = "0" + hour.toString();
    }
    else {
       hstr = hour.toString();
    }
    if(min < 10 ){
      mstr = "0" + min.toString();
    }
    else{
      mstr = min.toString();
    }
    return hstr+":"+mstr;
  }

  timerStringToMin(time):number {
    var arr = time.split(":");
    var min = parseInt(arr[0])*60 + parseInt(arr[1]);
    return min;
  }

}



