import { Routes, RouterModule } from '@angular/router';
import {SettingComponent} from "./setting.component";
import {ModuleWithProviders } from "@angular/core";
import { BasicConfigureComponent } from './basic-configure/basic-configure.component'
import { MenuListComponent } from './menu-list/menu-list.component'
import { LedTimerComponent } from './led-timer/led-timer.component'
import { AdvSettingComponent} from './adv-setting/adv-setting.component'
import { DataLoggerComponent } from './data-logger/data-logger.component'
export const setttingRoutes: Routes = [
    {
        path: '',
        component: SettingComponent,
        children: [
            { path: 'basic-config', component: BasicConfigureComponent },
            { path: 'menu-list', component: MenuListComponent },
            { path: 'led-timer', component: LedTimerComponent },
            { path: 'adv-setting', component: AdvSettingComponent },
            { path: 'data-logger', component: DataLoggerComponent }
        ]
    }
];

export const settingRouting:ModuleWithProviders = RouterModule.forChild(setttingRoutes);


